
* {
  border: none;
  margin: 0;
  padding: 0;
}
html, body {
  font-size: calc(100vw / 750 * 100);
  background: #00080b;
}
#app {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.shareDiv {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
}
.shareDiv >.start {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
  background: url('/static/img/bg-1.png') center / auto 100% no-repeat;
}
.shareDiv >.start >div {
  /* width: 3.98rem; */
  /* height: 1.75rem; */
  width: calc(398 / 1206 * 100vh);
  height: calc(175 / 1206 * 100vh);
  font-size: 0;
  position: absolute;
  left: 50%;
  margin-left: calc(-398 / 1206 * 100vh / 2);
  cursor: pointer;
}
.shareDiv >.start >.startP {
  bottom: calc(240 / 1206 * 100vh);
  background: url('/static/img/picture.png') center / auto 100% no-repeat;
}
.shareDiv >.start >.startV {
  bottom: calc(90 / 1206 * 100vh);
  background: url('/static/img/video.png') center / auto 100% no-repeat;
}
@media screen and (max-device-aspect-ratio: 750 / 1206) {
.shareDiv {
    height: calc(100vw * 1206 / 750);
    /* margin-top: calc(50vh - (100vw * 1206 / 750) / 2); */
}
.shareDiv >.start >div {
    width: 3.98rem;
    height: 1.75rem;
    margin-left: calc(-3.98rem / 2);
}
.shareDiv >.start >.startP {
    bottom: 2.4rem;
}
.shareDiv >.start >.startV {
    bottom: .9rem;
}
}

.pictureDiv {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background: #000;
}
.pictureDiv >.top{
  width: 100%;
  height: 100%;
}
.pictureDiv >.top >video{
  width: 100vw;
  height: 100vh;
  -o-object-fit: cover;
     object-fit: cover;
}
.pictureDiv >.bottom{
  width: 100%;
  height: calc(254 / 750 * 100vw);
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 100;
  background: #000;
  border-top: 1px solid #166c91;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
}
.bottom >div {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  cursor: pointer;
}
.bottom >.photo {
  width: calc(104 / 750 * 100vw);
  height: calc(104 / 750 * 100vw);
  background: url('/static/img/photo-bg.png') center / auto 100% no-repeat;
  margin-left: calc(44 / 750 * 100vw);
  margin-bottom: calc(54 / 750 * 100vw);
}
.bottom >.photo >img {
  width: calc(74 / 750 * 100vw);
  height: calc(74 / 750 * 100vw);
  /* background: #f00; */
  border-radius: calc(37 / 750 * 100vw);
  /* margin-top: calc(10 / 750 * 100vw); */
  /* margin-bottom: calc(54 / 750 * 100vw); */
}
.bottom >.btn {
  width: calc(296 / 750 * 100vw);
  height: calc(251 / 750 * 100vw);
  cursor: auto;
  background: url('/static/img/btn-bg.png') center / auto 100% no-repeat;
}
.bottom >.btn >span {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: .24rem;
  letter-spacing: 2px;
  margin-top: calc(20 / 750 * 100vw);
  color: #fff;
}
.bottom >.btn >div {
  width: calc(145 / 750 * 100vw);
  height: calc(145 / 750 * 100vw);
  display: block;
  cursor: pointer;
  margin: calc(30 / 750 * 100vw) auto 0;
  background: url('/static/img/btn.png') center / auto 100% no-repeat;
}
.bottom >.metathesis {
  width: calc(104 / 750 * 100vw);
  height: calc(107 / 750 * 100vw);
  background: url('/static/img/metathesis-bg.png') center / auto 100% no-repeat;
  margin-right: calc(44 / 750 * 100vw);
  margin-bottom: calc(50 / 750 * 100vw);
}
.pictureDiv >.after {
  width: 100%;
  height: calc(500 / 750 * 100vw);
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 200;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.after >div {
